import {api} from '../../api/api.js'
export default {
	data() {
		return {
			windowWidth: getApp().globalData.windowWidth,
			windowHeight: getApp().globalData.windowHeight,
			top: getApp().globalData.top,
			session_id:getApp().globalData.session_id,
			uid:getApp().globalData.uid,
			active:false,
			psd:'',
			upsd:'',
			upsds:''
		}
	},
	methods: {
		toBack() {
			uni.navigateBack();
		},
		resetPsd() {
			if(this.psd === null || this.psd === '' || this.psd === undefined) {
				uni.showToast({
					title:"请输入旧密码",
					icon:"none",
					duration:1500
				})
				return;
			}else if(this.upsd === null || this.upsd === '' || this.upsd === undefined) {
				uni.showToast({
					title:"请输入新密码",
					icon:"none",
					duration:1500
				})
				return;
			}else if(this.upsds === null || this.upsds === '' || this.upsds === undefined) {
				uni.showToast({
					title:"请确认新密码",
					icon:"none",
					duration:1500
				})
				return;
			}else if(this.upsds!= this.upsd ) {
				uni.showToast({
					title:"密码请保持一致",
					icon:"none",
					duration:1500
				})
				return;
			}
			uni.request({
				url:api.reset_pwd,
				data: {
					uid:this.uid,
					session_id:this.session_id,
					PassWord:this.psd,
					newPassWord:this.upsd,
					cnewPassWord:this.upsds
				},
				method:"post",
				header: {
					"Content-Type": "application/x-www-form-urlencoded"
				},
				success: (res) => {
					console.log(res.data);
					if(res.data.code === "success") {
						uni.showToast({
							title:res.data.msg,
							icon:"none",
							duration:1500
						})
					}else {
						uni.showToast({
							title:res.data.msg,
							icon:"none",
							duration:1500
						})
					}
				}
			})
		}
	}
}
